import React, { useState } from "react";
import styled from "styled-components";
import UseAnimations from "react-useanimations";
import arrowDown from "react-useanimations/lib/arrowDown";
import { About } from "./About";
import { Experience } from "./Experience";
import { Tech } from "./Tech";

const Content = styled.div`
  color: white;
  grid-area: 2 / 2 / 7 / 5;
  padding-top: 5px;
  padding-bottom: 10px;
  @media (min-width: 1400px) {
    padding: 40px;
  }
`;

const ButtonStyles = styled.div`
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  @media (min-width: 480px) {
    font-size: 22px;
  }
  @media (min-width: 1400px) {
    font-size: 32px;
  }
  font-size: 14px;
  font-weight: 400;
  color: white;
  :hover {
    transition: font-weight 0.2s ease-in;
    font-weight: 900;
  }
`;

const AboutButton = styled(ButtonStyles)`
  grid-area: 1 / 2 / 2 / 3;
  display: flex;
  justify-content: center;
  align-content: center;
`;

const ExperienceButton = styled(ButtonStyles)`
  grid-area: 1 / 3 / 2 / 4;
  display: flex;
  justify-content: center;
  align-content: center;
`;

const TechButton = styled(ButtonStyles)`
  grid-area: 1 / 4 / 2 / 5;
  display: flex;
  justify-content: center;
  align-content: center;
`;

const ScrollButtonWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  grid-area: 10 / 2 / 9 / 5;
  margin-right: 0px;
`;

const ScrollTextWrapper = styled.div`
  @media (min-width: 1010px) {
    font-size: 24px;
    margin-top: 0px;
  }
  margin-top: 4px;
  font-size: 12px;
  color: white;
`;

const AnimationWrapper = styled.div`
  margin-top: 4px;
`;

export const Info = () => {
  const [active, setActive] = useState(0);

  const handleClick = (key: number) => {
    setActive(key);
  };

  return (
    <>
      <AboutButton onClick={() => handleClick(0)}>About</AboutButton>
      <ExperienceButton onClick={() => handleClick(1)}>
        Experience
      </ExperienceButton>
      <TechButton onClick={() => handleClick(2)}>Tech</TechButton>
      <Content>
        <About active={active} />
        <Experience active={active} />
        <Tech active={active} />
      </Content>
      <ScrollButtonWrapper>
        <ScrollTextWrapper>Contact me</ScrollTextWrapper>
        <AnimationWrapper>
          <UseAnimations animation={arrowDown} strokeColor="white" />
        </AnimationWrapper>
      </ScrollButtonWrapper>
    </>
  );
};
